import React, { useEffect, useState } from "react";
import "react-credit-cards/es/styles-compiled.css";
import Cards from "react-credit-cards";
import Step from "./Step";
import { Link } from "react-router-dom";
import axios from "axios";
import {
  MDBInput,
  MDBCol,
  MDBRow,
  MDBBtn,
  MDBModal,
  MDBModalBody,
  MDBModalHeader,
  MDBCardHeader,
  MDBModalFooter,
  MDBContainer,
} from "mdbreact";
import BackBtn from "../../assets/back-btn.png";
import {
  AcceptHosted,
  FormContainer,
  FormComponent,
} from "react-authorize-net";
export default function PaymentForm(props) {
  const { state, onChangeField, prevStep, nextStep, history } = props;
  const [touched, setTouched] = useState(false);
  const [errorMsg, setErrorMsg] = useState("");
  const [touchcard, settouchcard] = useState(false);
  const [touchcvv, settouchcvv] = useState(false);
  const [touchname, settouchname] = useState(false);
  const [count, setcount] = useState(0);
  const [touchexp, settouchexp] = useState(false);
  const [failcount, setfail] = useState(0);
  const [modal, setmodal] = useState(true);
  const formRef = new React.createRef();

  const cardnumberValidate = (num) => {
    setTouched(true);
    settouchcard(true);
    setcount(count + 1);
    var rgx = new RegExp("^[0-9]+$");
    const isvalid = num.length > 12 && num.length < 20 && rgx.test(num);
    setErrorMsg(isvalid ? "" : "Please enter valid Credit card number ");
  };

  const cvvvalidate = (num) => {
    setTouched(true);
    settouchcvv(true);
    var rgx = new RegExp("^[0-9]+$");
    const isvalid = state.cvv.length > 1 && rgx.test(num);
    console.log(num.length);
    console.log(num);
    setcount(count + 1);
    console.log(isvalid);
    setErrorMsg(isvalid ? "" : "Please enter valid CVV ");
  };

  const validate = () => {
    setTouched(true);
    const isValid =
      formRef.current.checkValidity() &&
      touchcard &&
      touchcvv &&
      touchexp &&
      touchname;

    setErrorMsg(isValid ? "" : "Please enter valid details");

    // if (!isValid) {
    //   setErrorMsg("Please enter valid details");
    // }
    // if (state.cvv.length < 2) {
    //   setErrorMsg("Please enter valid CVV");
    // } else if (state.creditCardNumber.length < 8) {
    //   setErrorMsg("Please enter valid credit card number");
    // } else {
    //   setErrorMsg("");
    // }
  };

  // const clientid = null;

  // const apiLoginId = "4w9FZ3hNf";
  const [showModal, setShowModal] = useState(true);

  const toggleModal = () => {
    setShowModal(!showModal);
  };

  const toggle = () => {
    setmodal(!modal);
  };

  const clientid =
    "6W6WXugjspWA9Z33smVN4jN6wA86WaBfpe38Bh4F9MnZkX4FA7bPCXkVEtTWGGff";
  const apiLoginId = "6VSdc64g";

  const onErrorHandler = (response) => {
    console.log(response + "fffff");
  };

  const onSuccessHandler = (response) => {
    // Process API response on your backend...
    console.log(JSON.stringify(response) + "ssssss");
  };

  const submitHandler = () => {
    var config = {
      headers: {
        "Content-Type": "application/json",
        "Accept-Encoding": "gzip, deflate, br",
        "Access-Control-Allow-Origin": "*",
      },
    };

    //api call
    const data = {
      amount: "97",
      card_number: state.creditCardNumber,
      cvv: state.cvv,
      name: state.nameOnCard,
      zipcode: state.paymentzipcode,
      month: state.exp.substring(0, 1),
      year: state.exp.substring(3, 4),
    };
    axios
      .post(
        "http://migrationade.com/api/CreatePayment",
        JSON.stringify(data),
        config
      )
      .then((response) => console.log(response))
      .finally(() => console.log("Data Sent", { data: data }));
    let response = "failure";
    if (response == "failure") setfail(failcount + 1);
    if (response == "success") history.push(nextStep);
  };

  const handleInputFocus = (e) => {
    onChangeField(e, "focus", e.target.name);
  };

  useEffect(() => {
    setTouched(formRef.current.checkValidity());
  }, []);

  return (
    <>
      <Step
        heading={`Fantastic! That's everything I just need to collect your payment information and have you agree to our terms of service.`}
      >
        <img
          src={BackBtn}
          className="btn-back"
          onClick={() => history.push(prevStep)}
        />
        <div id="PaymentForm">
          <Cards
            cvc={state.cvv}
            expiry={state.exp}
            focused={state.focus}
            name={state.nameOnCard}
            number={state.creditCardNumber}
          />
          {/* <FormContainer
          environment="sandbox"
          onError={onErrorHandler}
          onSuccess={onSuccessHandler}
          amount={23}
          className="formcontainer"
          component={FormComponent}
          clientKey={clientid}
          apiLoginId={apiLoginId}
        /> */}
          {/* <AcceptHosted formToken={"..."}>
          <button>Pay now</button>
        </AcceptHosted> */}
          <form ref={formRef}>
            <MDBRow center>
              <MDBCol md={4}>
                <MDBInput
                  type="tel"
                  name="creditCardNumber"
                  outline
                  label="Card Number"
                  pattern="^[0-9]+$"
                  placeholder="Card Number"
                  value={state.creditCardNumber}
                  onChange={(event) => {
                    validate();

                    if (event.target.value.length > 20) return;
                    onChangeField(
                      event,
                      "creditCardNumber",
                      event.target.value
                    );
                    cardnumberValidate(state.creditCardNumber);
                  }}
                  onFocus={(e) => handleInputFocus(e)}
                  icon="credit-card"
                  required
                />
              </MDBCol>
            </MDBRow>
            <MDBRow center>
              <MDBCol md={2}>
                <MDBInput
                  type="tel"
                  name="exp"
                  outline
                  className="form-control"
                  label="expiry"
                  placeholder="MM/YY"
                  value={state.exp}
                  icon="calendar-alt"
                  containerClass="mt-0 "
                  required
                  onChange={(event) => {
                    validate();

                    var rgx = new RegExp("^[0-9]+$");
                    settouchexp(true);
                    var value = event.target.value;
                    value = value.slice(0, 5);
                    setcount(count + 1);
                    onChangeField(
                      event,
                      "exp",
                      _handlingCardExpiry(value, state)
                    );
                    setErrorMsg(
                      rgx.test(event.target.value.substring(0, 1)) &&
                        state.exp.length > 3
                        ? ""
                        : "Please Enter a valid expiry date"
                    );
                  }}
                  onFocus={(e) => handleInputFocus(e)}
                />
              </MDBCol>
              <MDBCol md={2}>
                <MDBInput
                  label="CVV"
                  type="tel"
                  maxLength="3"
                  containerClass="mt-0 "
                  name="cvv"
                  outline
                  value={state.cvv}
                  required
                  onFocus={(e) => handleInputFocus(e)}
                  onChange={(event) => {
                    var rgx = new RegExp("^[0-9]+$");

                    var { value, maxLength } = event.target;
                    validate();
                    value = value.slice(0, maxLength);
                    onChangeField(event, "cvv", value);
                    cvvvalidate(state.cvv);
                  }}
                  icon="lock"
                />
              </MDBCol>
            </MDBRow>
            <MDBRow center className="mt-0">
              <MDBCol md={4} className="mt-0">
                <MDBInput
                  type="text"
                  containerClass="mt-0 "
                  name="nameOnCard"
                  outline
                  required
                  label="Name on card"
                  placeholder="name"
                  icon="user"
                  required
                  value={state.nameOnCard}
                  onChange={(event) => {
                    setcount(count + 1);
                    settouchname(true);
                    validate();
                    onChangeField(event, "nameOnCard", event.target.value);
                  }}
                  onFocus={(e) => handleInputFocus(e)}
                />
              </MDBCol>
            </MDBRow>
            <MDBRow center className="mt-0">
              <MDBCol md={4} className="mt-0">
                <MDBInput
                  type="text"
                  containerClass="mt-0 "
                  name="paymentzipcode"
                  outline
                  required
                  label="Zip Code"
                  placeholder=""
                  pattern="^[0-9]+$"
                  required
                  value={state.zipcode}
                  onChange={(event) => {
                    setcount(count + 1);
                    settouchname(true);
                    validate();
                    onChangeField(event, "paymentzipcode", event.target.value);
                    setErrorMsg(
                      state.paymentzipcode.length > 6
                        ? "Enter a valid zip code"
                        : ""
                    );
                  }}
                  onFocus={(e) => handleInputFocus(e)}
                />
              </MDBCol>
            </MDBRow>
          </form>
        </div>
        <MDBRow center>
          <MDBRow center className="d-flex flex-column align-items-center">
            {touched && errorMsg.length > 0 && (
              <div className="text-danger mb-2">{errorMsg}</div>
            )}
            {touched &&
              touchcard &&
              touchcvv &&
              touchexp &&
              touchname &&
              !errorMsg && (
                <MDBBtn
                  color="pink"
                  rounded
                  type="button"
                  className="z-depth-1a"
                  onClick={() => submitHandler()}
                >
                  Next
                </MDBBtn>
              )}
            {(!touched ||
              !touchcard ||
              !touchcvv ||
              !touchexp ||
              !touchname ||
              errorMsg) && (
              <MDBBtn
                color={"grey"}
                disabled
                rounded
                type="button"
                className="z-depth-1a"
              >
                Next
              </MDBBtn>
            )}
          </MDBRow>
        </MDBRow>
      </Step>
      <MDBContainer>
        <MDBModal isOpen={modal} toggle={toggle}>
          <MDBModalHeader toggle={toggle}>Oops!</MDBModalHeader>
          <MDBModalBody>
            Unfortunately, we were unable to process the credit card details you
            provided please try again
          </MDBModalBody>
          <MDBModalFooter>
            <MDBBtn color="secondary" onClick={toggle}>
              Close
            </MDBBtn>
          </MDBModalFooter>
        </MDBModal>
      </MDBContainer>
    </>
  );
}

const _handlingCardExpiry = (text, state) => {
  if (text.indexOf(".") >= 0 || text.length > 5) {
    // Since the keyboard will have a decimal and we don't want
    // to let the user use decimals, just exit if they add a decimal
    // Also, we only want 'MM/YY' so if they try to add more than
    // 5 characters, we want to exit as well
    return;
  }

  if (text.length === 2 && state.exp.length === 1) {
    // This is where the user has typed 2 numbers so far
    // We can manually add a slash onto the end
    // We check to make sure the current value was only 1 character
    // long so that if they are backspacing, we don't add on the slash again
    text += "/";
  }

  // Update the state, which in turns updates the value in the text field
  return text;
};

function validatenum(evt) {
  var theEvent = evt || window.event;

  // Handle paste
  if (theEvent.type === "paste") {
    key = evt.clipboardData.getData("text/plain");
  } else {
    // Handle key press
    var key = theEvent.keyCode || theEvent.which;
    key = String.fromCharCode(key);
  }
  var regex = /[0-9]|\./;
  if (!regex.test(key)) {
    theEvent.returnValue = false;
    if (theEvent.preventDefault) theEvent.preventDefault();
  }
}
